.main-description {
  background-color: #012f34;
  background-image: url("../../assets/img/description/bg-pattern-home-3.png");
  background-repeat: no-repeat;
  background-position: top right;
  min-height: 100vh;

  @include for-narrow-layout {
    background-position: bottom right;
  }

  .description-container {
    flex-wrap: wrap;

    @include for-narrow-layout {
      flex-wrap: nowrap;
    }
  }

  .title-container {
    margin-inline-end: rem(150px);

    @include for-mobile-layout {
      min-width: 100%;
    }

    img {
      margin-block-end: rem(40px);
      width: 30px;

      @include for-mobile-layout {
        margin-block-start: 4rem;
      }
    }

    h2 {
      font-size: rem(32px);
      font-family: livvic-semi-bold;
      line-height: 1;
      max-width: 13ch;

      @include for-narrow-layout {
        font-size: rem(48px);
        max-width: 15ch;
      }
    }
  }

  .article-container {
    flex-direction: column;
    text-align: center;
    margin-block-end: rem(40px);

    &:first-child {
      margin-block-start: rem(40px);
    }

    @include for-narrow-layout {
      flex-direction: row;
      text-align: left;
    }

    img {
      margin-inline-end: rem(20px);
      height: 70px;

      @include for-mobile-layout {
        margin: 0 auto rem(20px);
        width: 65px;
      }
    }

    .title {
      color: #f67e7e;
      font-size: rem(18px);
      font-family: livvic-semi-bold;
      margin-block-end: rem(16px);
    }

    .text {
      max-width: 52ch;
      font-size: rem(15px);
      font-family: livvic-semi-bold;
      line-height: 2;
    }
  }
}
