// App Header
.hamburger-icn {
  &::before {
    content: "\f0c9";
  }

  @include for-narrow-layout {
    display: none;
  }
}

.close-icn {
  font-size: rem(18px);
  text-align: right;
  margin-inline: rem(21px);
  margin-block-end: rem(41px);

  &::before {
    content: "\f00d";
  }

  @include for-narrow-layout {
    display: none;
  }
}

.facebook-icn::before{
  content: "\f082";
}

.pinterest-icn::before{
  content: "\f0d2";
}

.twiter-icn::before{
  content: "\f099";
}
