.main-hero {
  background-color: #014e56;
  font-family: livvic-semi-bold;
  height: 85vh;

  @include for-narrow-layout {
    background-image: url("../../assets/img/hero/background-img1.png");
    background-repeat: no-repeat;
    background-position: left center;
  }

  .backgrounder {
    background-image: url("../../assets/img/hero/background-img2.svg");
    background-repeat: no-repeat;
    background-position: center bottom;
    height: inherit;

    @include for-narrow-layout {
      background-position: right bottom;
    }
  }

  .full-height {
    height: 100%;

    @include for-narrow-layout {
      flex-wrap: nowrap;
    }

    img {
      height: 5px;
      width: 50px;

      @include for-mobile-layout {
        display: none;
      }
    }

    p {
      font-size: rem(15px);
      max-width: 39ch;
      text-align: center;

      @include for-mobile-layout {
        margin-block-start: -13.5rem;
      }

      @include for-narrow-layout {
        font-size: rem(18px);
        margin-block-start: 8rem;
        max-width: 40ch;
        text-align: left;
      }
    }
  }

  h2 {
    font-size: rem(40px);
    text-align: center;
    margin: 0 auto;
    max-width: 10ch;

    @include for-narrow-layout {
      font-size: rem(100px);
      text-align: left;
    }

    span {
      color: #f67e7e;
    }
  }
}
