.main-nav {
  background-color: #014e56;
  padding-block-start: rem(60px);

  @include for-narrow-layout {
    padding-block-start: rem(73px);
  }

  img {
    height: 40px;

    @include for-narrow-layout{
      margin-inline-end: rem(70px);
    }
  }

  .links-container {
    position: absolute;
    right: 0;
    top: 0;
    background-color: #2c6269;
    background-image: url("../../assets/img/app-header/mobile-background.png");
    background-size: 90px;
    background-repeat: no-repeat;
    background-position: bottom right;
    background-clip: border-box;
    padding-block-start: rem(21px);
    height: 100vh;
    width: 68vw;
    transition: all ease-in-out $t-fast;

    &.closed-nav {
      right: -100vw;
    }

    &.open-nav {
      right: 0;
      box-shadow: 1px 1px 1px 3000px rgba($black, 0.7);
    }

    @include for-narrow-layout {
      position: static;
      background-color: transparent;
      background-image: none;
      padding-block-start: 0;
      height: fit-content;
    }
  }

  .list-container {
    font-size: rem(18px);
    font-family: livvic-semi-bold;
    margin-inline: rem(38px);

    @include for-narrow-layout {
      display: flex;
      position: relative;
      align-items: center;
      margin: 0;

      & li:first-child{
        margin-inline-end: rem(36px);
      }
    }

    & > *:not(:last-child) {
      margin-block-end: rem(36px);

      @include for-narrow-layout {
        margin-block-end: 0;
      }
    }

    & :last-child {
      border: 2px solid $white;
      @include large-rounded-corners;
      text-align: center;
      padding-block: rem(10px);

      @include for-narrow-layout{
        position: absolute;
        right: 0;
        bottom: 0;
        padding-block: rem(5px);
        width: rem(155px);
      }
    }
  }
}
