.get-started {
  background-color: #f67e7e;
  background-image: url("../../assets/img/get-started/background.svg");
  background-repeat: no-repeat;
  background-position: bottom left;

  .get-started-container {
    padding-inline: rem(90px);
    height: rem(200px);

    @include for-mobile-layout {
      flex-direction: column;
      justify-content: center;
      gap: 20px;
      height: rem(350px);
    }
  }

  h2 {
    color: #012f34;
    font-size: rem(32px);
    font-family: livvic-semi-bold;

    @include for-mobile-layout {
      text-align: center;
    }
  }

  span {
    border: 2px solid #012f34;
    border-radius: 70px;
    color: #012f34;
    font-size: rem(18px);
    font-family: livvic-semi-bold;
    padding: 15px 32px;
  }
}
