.main-footer {
  background-color: #002529;
  padding-block: rem(50px);

  & > div {
    @include for-mobile-layout {
      flex-wrap: wrap;
    }
    & > div {
      @include for-mobile-layout {
        flex-wrap: wrap;
        margin: 0 auto;
      }
    }
  }

  .inside-links-container {
    margin-inline-end: rem(100px);

    @include for-mobile-layout {
      margin: 0 auto;
    }
  }

  img {
    height: 40px;

    @include for-mobile-layout {
      margin-block-end: rem(25px);
    }
  }

  .inside-links {
    font-size: rem(18px);
    font-family: livvic-semi-bold;

    &:hover {
      cursor: pointer;
      color: #f67e7e;
    }

    @include for-mobile-layout {
      margin-block-end: rem(25px);
    }
  }

  .contact-container {
    color: rgba($white, 0.6);
    font-size: rem(15px);
    font-family: livvic-semi-bold;
    line-height: 2;

    @include for-mobile-layout {
      flex-basis: 100%;
      text-align: center;
      margin-block-end: rem(40px);
    }
  }

  .social-networks-container {
    align-self: flex-end;
    gap: rem(16px);
    font-size: rem(24px);

    & > *:hover {
      cursor: pointer;
      color: #f67e7e;
    }

    @include for-mobile-layout{
        align-self: center;
    }
  }

  .copyrights {
    color: rgba($white, 0.6);
    font-size: rem(15px);
    font-family: livvic-semi-bold;

    @include for-mobile-layout {
      margin-block-start: rem(16px);
    }
  }
}
